<template>
  <div class="container-main">
    <div class="container-form">
      <h2>Login</h2>

      <input class="input-form" placeholder="Email" v-model="email" type="email">
      <input class="input-form" placeholder="Senha" v-model="senha" type="password">
      <p @click="logarComOutrosProvedores">Esqueceu sua senha?</p>

      <!-- <button class="btn-entrar">Entrar</button> -->
      <v-btn round color="success" class="btn-entrar" v-on:click="logar" :loading="loading">Entrar</v-btn>
      <p id="cadastro">
        Não é cadastro?
        <span @click="logarComOutrosProvedores">Cadastra-se</span>
      </p>
      <p id="ou">ou</p>
      <hr>
      <div class="container-btn">
        <button @click="logarComOutrosProvedores">Facebook</button>
        <button @click="logarComOutrosProvedores">Google</button>
      </div>
    </div>
    <div class="container-logo">
      <div class="container-cover">
        <h1>
          Bem vindo a
          <b>Moobility</b>
        </h1>
        <p>Acreditamos em dados e os nossos dados acreditam no seu negocio</p>
      </div>
    </div>
    <v-snackbar
      v-model="snackbar"
      :color="color"
      :multi-line="mode === 'multi-line'"
      :timeout="timeout"
      :vertical="mode === 'vertical'"
    >
      {{ text }}
      <v-btn dark flat @click="snackbar = false">Fechar</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import firebase from "../firebase";
export default {
  name: "HelloWorld",
  props: {
    msg: String
  },

  data() {
    return {
      loading: false,
      email: "",
      senha: "",
      snackbar: false,
      color: "",
      mode: "",
      timeout: 6000,
      text: "Hello, I'm a snackbar"
    };
  },

  methods: {
    logar() {
      this.loading = true;
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.senha)
        .then(user => {
          console.log("entrou", user);
          this.color = "#00cc00"
          this.text = "Login aceito!";
        })
        .catch(err => {
          console.error(err);
          this.color = "#ff0000"
          this.text = err.message;
        })
        .finally(() => {
          this.snackbar = true;
          this.loading = false;
        });
    },

    logarComOutrosProvedores(){
      this.snackbar = true;
      this.color = "#000";
      this.text = "Funcionalidade não adicionada"
    }
  },

  
};
</script>

